import './App.css';
import Home from "./views/Home/Home"
import Landing from "./views/Landing/Landing"
import {Route} from "react-router-dom"

function App() {
  return (
    <div className="App">
      <h1>Henry Food</h1>
      <Route path="/home">
         <Home/>
      </Route>
      <Route path="/">
          <Landing/>
      </Route>
      
    </div>
  );
}

export default App;
